.c-portfolio__heading {
  text-align: center;
  text-transform: none;
  margin: ($base-spacing-unit * 4) auto ($base-spacing-unit * 5) auto;
  padding: 0 5%;

  br {
    display: none;
  }

  @include breakpoint($break-tablet) {
    // max-width: 600px;
    @include font-size(42px);

    br {
      display: block;
    }
  };
}

.c-portfolio {
  float: left;

  .c-featured__single {
    height: auto;
    float: left;

    @include breakpoint($break-tablet) {
      width: 50%;

    };

    @include breakpoint($break-desktop-med) {
      width: 33.33333%;
    };
  }
}
