@font-face {
  font-family: 'fontello';
  src: url('../font/fontello.eot?77013932');
  src: url('../font/fontello.eot?77013932#iefix') format('embedded-opentype'),
       url('../font/fontello.woff2?77013932') format('woff2'),
       url('../font/fontello.woff?77013932') format('woff'),
       url('../font/fontello.ttf?77013932') format('truetype'),
       url('../font/fontello.svg?77013932#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
